@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-size:14px;font-family:'Raleway', sans-serif}body{background-color:#0079bf !important}.footer{position:absolute;bottom:0;left:0;right:0;margin-left:auto;margin-right:auto;color:white;font-weight:bold;background-color:black;opacity:0.7;padding-top:5px}.footer a{color:white;text-decoration:none}

